import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { useToast } from "@/hooks/use-toast";
import { useAuth } from "@/contexts/AuthContext";
import i18n from "@/i18n/config";

// Helper function to get user role
const getUserRole = async (userId: string) => {
  const { data } = await supabase
    .from("user_roles")
    .select("role")
    .eq("user_id", userId)
    .single();
  return data?.role;
};

export const useAdminUsers = () => {
  const { user } = useAuth();
  
  return useQuery({
    queryKey: ["admin-users", user?.id],
    queryFn: async () => {
      if (!user) return [];
      
      // Fetch profiles and roles in parallel
      const [profilesResult, rolesResult, sessionResult, currentUserRoleResult] = await Promise.all([
        supabase
          .from("profiles")
          .select("*")
          .order("created_at", { ascending: false }),
        supabase
          .from("user_roles")
          .select("user_id, role"),
        supabase.auth.getSession(),
        supabase
          .from("user_roles")
          .select("role")
          .eq("user_id", user.id)
          .single()
      ]);

      if (profilesResult.error) throw profilesResult.error;
      
      const profiles = profilesResult.data || [];
      const roles = rolesResult.data || [];
      
      // Create a role map for O(1) lookup
      const roleMap: Record<string, string> = {};
      roles.forEach(r => {
        roleMap[r.user_id] = r.role;
      });

      // Fetch user emails and auth data from edge function
      let emailMap: Record<string, string> = {};
      let createdAtMap: Record<string, string> = {};
      let lastSignInMap: Record<string, string | null> = {};
      
      if (sessionResult.data?.session?.access_token) {
        try {
          const response = await supabase.functions.invoke("get-user-emails", {
            headers: {
              Authorization: `Bearer ${sessionResult.data.session.access_token}`,
            },
          });
          if (response.data?.emails) {
            emailMap = response.data.emails;
          }
          if (response.data?.createdAt) {
            createdAtMap = response.data.createdAt;
          }
          if (response.data?.lastSignIn) {
            lastSignInMap = response.data.lastSignIn;
          }
        } catch (e) {
          console.error("Failed to fetch user data:", e);
        }
      }

      // Map profiles with roles (no additional queries needed)
      const usersWithRoles = profiles.map((profile) => ({
        ...profile,
        role: roleMap[profile.id] || "user",
        email: emailMap[profile.id] || "",
        auth_created_at: createdAtMap[profile.id] || profile.created_at,
        last_sign_in_at: lastSignInMap[profile.id] || null,
      }));

      // Check if current user is supervisor
      const currentUserRole = currentUserRoleResult.data;

      // If supervisor, show merchants they manage
      if (currentUserRole?.role === "supervisor") {
        // Get all restaurants managed by this supervisor
        const { data: supervisorRestaurants, error: restaurantError } = await supabase
          .from("merchant_restaurants")
          .select("restaurant_id")
          .eq("user_id", user.id);

        if (restaurantError) throw restaurantError;

        if (!supervisorRestaurants || supervisorRestaurants.length === 0) {
          return [];
        }

        const restaurantIds = supervisorRestaurants.map(r => r.restaurant_id);

        // Get all merchants who manage any of these restaurants
        const { data: merchantRestaurantLinks, error: linksError } = await supabase
          .from("merchant_restaurants")
          .select("user_id, restaurant_id")
          .in("restaurant_id", restaurantIds);

        if (linksError) throw linksError;

        if (!merchantRestaurantLinks || merchantRestaurantLinks.length === 0) {
          return [];
        }

        // Get unique merchant user IDs (excluding the supervisor)
        const merchantIds = [...new Set(
          merchantRestaurantLinks
            .map(link => link.user_id)
            .filter(id => id !== user.id)
        )];

        if (merchantIds.length === 0) {
          return [];
        }

        // Filter users who are merchants from the role map
        const validMerchantIds = merchantIds.filter(id => roleMap[id] === "merchant");

        // Filter users to show only valid merchants
        return usersWithRoles.filter(u => validMerchantIds.includes(u.id));
      }

      return usersWithRoles;
    },
    staleTime: 30000, // Cache for 30 seconds
  });
};

export const useChangeRole = () => {
  const { toast } = useToast();
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async ({ userId, role }: { userId: string; role: "user" | "merchant" | "supervisor" | "admin" }) => {
      // Use upsert to handle both insert and update cases
      const { error } = await supabase
        .from("user_roles")
        .upsert(
          { user_id: userId, role },
          { onConflict: 'user_id' }
        );

      if (error) throw error;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["admin-users"] });
      queryClient.invalidateQueries({ queryKey: ["user-roles"] });
      toast({
        title: i18n.t("common.success"),
        description: i18n.t("toast.roleChanged"),
      });
    },
    onError: (error: any) => {
      console.error("Role change error:", error);
      toast({
        title: i18n.t("common.error"),
        description: error.message || i18n.t("toast.roleChangeFailed"),
        variant: "destructive",
      });
    },
  });
};

