// Hook to get restaurants based on user role (admin sees all, supervisor sees only their restaurants)
export const useAdminRestaurants = () => {
  const { user } = useAuth();
  
  return useQuery({
    queryKey: ["admin-restaurants", user?.id],
    queryFn: async () => {
      if (!user) return [];

      const userRole = await getUserRole(user.id);
      const isSupervisor = userRole === "supervisor";

      if (isSupervisor) {
        // Get supervisor's assigned restaurants
        const { data: merchantRestaurants, error: mrError } = await supabase
          .from("merchant_restaurants")
          .select("restaurant_id")
          .eq("user_id", user.id);

        if (mrError) throw mrError;

        const restaurantIds = merchantRestaurants?.map(mr => mr.restaurant_id) || [];
        
        if (restaurantIds.length === 0) return [];

        // Get full restaurant details for assigned restaurants
        const { data, error } = await supabase
          .from("restaurants")
          .select("*")
          .in("id", restaurantIds)
          .order("rating", { ascending: false });

        if (error) throw error;
        return data || [];
      } else {
        // Admin sees all restaurants
        const { data, error } = await supabase
          .from("restaurants")
          .select("*")
          .order("rating", { ascending: false });

        if (error) throw error;
        return data || [];
      }
    },
    enabled: !!user,
  });
};

export const useOperationLogs = () => {
  return useQuery({
    queryKey: ["operation-logs"],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("operation_logs")
        .select("*")
        .order("created_at", { ascending: false })
        .limit(100);

      if (error) throw error;

      // Fetch user profiles for logs
      const logsWithUsers = await Promise.all(
        (data || []).map(async (log) => {
          const { data: profile } = await supabase
            .from("profiles")
            .select("display_name")
            .eq("id", log.user_id)
            .single();

          return {
            ...log,
            user_name: profile?.display_name || "Unknown User",
          };
        })
      );

      return logsWithUsers;
    },
  });
};

export const useUpdateMerchantProfile = () => {
  const { toast } = useToast();
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async ({
      userId,
      display_name,
      phone,
      avatar_url,
    }: {
      userId: string;
      display_name?: string;
      phone?: string;
      avatar_url?: string;
    }) => {
      const { data, error } = await supabase
        .from("profiles")
        .update({ display_name, phone, avatar_url })
        .eq("id", userId)
        .select()
        .single();

      if (error) throw error;
      return data;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["admin-users"] });
      toast({
        title: i18n.t("common.success"),
        description: i18n.t("toast.profileUpdated"),
      });
    },
    onError: () => {
      toast({
        title: i18n.t("common.error"),
        description: i18n.t("toast.profileUpdateFailed"),
        variant: "destructive",
      });
    },
  });
};

export const useUploadMerchantAvatar = () => {
  const { toast } = useToast();

  return useMutation({
    mutationFn: async ({ file, userId }: { file: File; userId: string }) => {
      const fileExt = file.name.split(".").pop();
      const fileName = `${Date.now()}.${fileExt}`;
      const filePath = `${userId}/${fileName}`;

      const { error: uploadError } = await supabase.storage
        .from("avatars")
        .upload(filePath, file, { upsert: true });

      if (uploadError) throw uploadError;

      const { data: { publicUrl } } = supabase.storage
        .from("avatars")
        .getPublicUrl(filePath);

      return publicUrl;
    },
    onError: () => {
      toast({
        title: i18n.t("common.error"),
        description: i18n.t("toast.avatarUploadFailed"),
        variant: "destructive",
      });
    },
  });
};

export const useAdminStats = () => {
  const { user } = useAuth();
  
  return useQuery({
    queryKey: ["admin-stats", user?.id],
    queryFn: async () => {
      if (!user) return {
        totalUsers: 0,
        totalRestaurants: 0,
        totalActivities: 0,
        totalComments: 0,
      };

      const userRole = await getUserRole(user.id);
      const isSupervisor = userRole === "supervisor";

      if (isSupervisor) {
        // Get supervisor's assigned restaurants
        const { data: merchantRestaurants } = await supabase
          .from("merchant_restaurants")
          .select("restaurant_id, user_id")
          .eq("user_id", user.id);

        const restaurantIds = merchantRestaurants?.map(mr => mr.restaurant_id) || [];
        const merchantUserIds = Array.from(new Set(merchantRestaurants?.map(mr => mr.user_id))) || [];

        // Count only merchants managed by this supervisor
        const merchantsCount = merchantUserIds.length;

        // Count only assigned restaurants
        const restaurantsCount = restaurantIds.length;

        // Count comments for assigned restaurants
        let commentsCount = 0;
        if (restaurantIds.length > 0) {
          const { count } = await supabase
            .from("comments")
            .select("*", { count: "exact", head: true })
            .eq("target_type", "restaurant")
            .in("target_id", restaurantIds);
          commentsCount = count || 0;
        }

        return {
          totalUsers: merchantsCount,
          totalRestaurants: restaurantsCount,
          totalActivities: 0, // Supervisors don't manage activities
          totalComments: commentsCount,
        };
      }

      // Admin sees all stats
      const [usersCount, restaurantsCount, activitiesCount, commentsCount] =
        await Promise.all([
          supabase.from("profiles").select("*", { count: "exact", head: true }),
          supabase.from("restaurants").select("*", { count: "exact", head: true }),
          supabase.from("activities").select("*", { count: "exact", head: true }),
          supabase.from("comments").select("*", { count: "exact", head: true }),
        ]);

      return {
        totalUsers: usersCount.count || 0,
        totalRestaurants: restaurantsCount.count || 0,
        totalActivities: activitiesCount.count || 0,
        totalComments: commentsCount.count || 0,
      };
    },
  });
};

export const useUserGrowth = () => {
  const { user } = useAuth();
  
  return useQuery({
    queryKey: ["user-growth", user?.id],
    queryFn: async () => {
      if (!user) return [];

      const userRole = await getUserRole(user.id);
      const isSupervisor = userRole === "supervisor";

      if (isSupervisor) {
        // Get supervisor's managed merchants
        const { data: merchantRestaurants } = await supabase
          .from("merchant_restaurants")
          .select("user_id")
          .eq("user_id", user.id);

        const merchantUserIds = Array.from(new Set(merchantRestaurants?.map(mr => mr.user_id)));
        
        if (merchantUserIds.length === 0) return [];

        // Get merchant profiles
        const { data, error } = await supabase
          .from("profiles")
          .select("created_at")
          .in("id", merchantUserIds)
          .order("created_at", { ascending: true });

        if (error) throw error;

        // Group by month
        const monthlyData: Record<string, number> = {};
        data?.forEach((profile) => {
          const date = new Date(profile.created_at);
          const monthKey = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, "0")}`;
          monthlyData[monthKey] = (monthlyData[monthKey] || 0) + 1;
        });

        // Convert to cumulative data
        let cumulative = 0;
        const result = Object.entries(monthlyData)
          .sort(([a], [b]) => a.localeCompare(b))
          .map(([month, count]) => {
            cumulative += count;
            return { month, count: cumulative };
          });

        return result;
      }

      // Admin sees all user growth
      const { data, error } = await supabase
        .from("profiles")
        .select("created_at")
        .order("created_at", { ascending: true });

      if (error) throw error;

      // Group by month
      const monthlyData: Record<string, number> = {};
      data?.forEach((profile) => {
        const date = new Date(profile.created_at);
        const monthKey = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, "0")}`;
        monthlyData[monthKey] = (monthlyData[monthKey] || 0) + 1;
      });

      // Convert to cumulative data
      let cumulative = 0;
      const result = Object.entries(monthlyData)
        .sort(([a], [b]) => a.localeCompare(b))
        .map(([month, count]) => {
          cumulative += count;
          return { month, count: cumulative };
        });

      return result;
    },
  });
};

export const useTopRestaurants = () => {
  const { user } = useAuth();
  
  return useQuery({
    queryKey: ["top-restaurants", user?.id],
    queryFn: async () => {
      if (!user) return [];

      const userRole = await getUserRole(user.id);
      const isSupervisor = userRole === "supervisor";

      let query = supabase
        .from("restaurants")
        .select("id, name, rating")
        .order("rating", { ascending: false, nullsFirst: false })
        .limit(10);

      if (isSupervisor) {
        // Get supervisor's assigned restaurants
        const { data: merchantRestaurants } = await supabase
          .from("merchant_restaurants")
          .select("restaurant_id")
          .eq("user_id", user.id);

        const restaurantIds = merchantRestaurants?.map(mr => mr.restaurant_id) || [];
        
        if (restaurantIds.length === 0) return [];

        query = supabase
          .from("restaurants")
          .select("id, name, rating")
          .in("id", restaurantIds)
          .order("rating", { ascending: false, nullsFirst: false })
          .limit(10);
      }

      const { data, error } = await query;

      if (error) throw error;

      // Get comment counts for each restaurant
      const restaurantsWithComments = await Promise.all(
        (data || []).map(async (restaurant) => {
          const { count } = await supabase
            .from("comments")
            .select("*", { count: "exact", head: true })
            .eq("target_id", restaurant.id)
            .eq("target_type", "restaurant");

          return {
            name: restaurant.name,
            rating: restaurant.rating || 0,
            comments: count || 0,
          };
        })
      );

      return restaurantsWithComments;
    },
  });
};

export const useRestaurantRatingTrends = () => {
  const { user } = useAuth();
  
  return useQuery({
    queryKey: ["restaurant-rating-trends", user?.id],
    queryFn: async () => {
      if (!user) return [];

      const userRole = await getUserRole(user.id);
      const isSupervisor = userRole === "supervisor";

      // Get restaurants (filtered for supervisors)
      let restaurantIds: string[] = [];
      let restaurantNames: Record<string, string> = {};

      if (isSupervisor) {
        const { data: merchantRestaurants } = await supabase
          .from("merchant_restaurants")
          .select("restaurant_id")
          .eq("user_id", user.id);

        restaurantIds = merchantRestaurants?.map(mr => mr.restaurant_id) || [];
      } else {
        const { data: restaurants } = await supabase
          .from("restaurants")
          .select("id, name")
          .order("rating", { ascending: false, nullsFirst: false })
          .limit(5);

        restaurantIds = restaurants?.map(r => r.id) || [];
        restaurants?.forEach(r => {
          restaurantNames[r.id] = r.name;
        });
      }

      if (restaurantIds.length === 0) return [];

      // Get all ratings for these restaurants with timestamps
      const { data: ratings } = await supabase
        .from("comments")
        .select("target_id, rating_value, created_at")
        .eq("target_type", "restaurant")
        .in("target_id", restaurantIds)
        .not("rating_value", "is", null)
        .order("created_at", { ascending: true });

      if (!ratings || ratings.length === 0) return [];

      // Get restaurant names if not already fetched
      if (Object.keys(restaurantNames).length === 0) {
        const { data: restaurants } = await supabase
          .from("restaurants")
          .select("id, name")
          .in("id", restaurantIds);

        restaurants?.forEach(r => {
          restaurantNames[r.id] = r.name;
        });
      }

      // Group by restaurant and month, calculate average rating
      const monthlyAverages: Record<string, Record<string, { sum: number; count: number }>> = {};

      ratings.forEach(rating => {
        const date = new Date(rating.created_at);
        const monthKey = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, "0")}`;
        
        if (!monthlyAverages[rating.target_id]) {
          monthlyAverages[rating.target_id] = {};
        }
        
        if (!monthlyAverages[rating.target_id][monthKey]) {
          monthlyAverages[rating.target_id][monthKey] = { sum: 0, count: 0 };
        }
        
        monthlyAverages[rating.target_id][monthKey].sum += rating.rating_value;
        monthlyAverages[rating.target_id][monthKey].count += 1;
      });

      // Convert to chart data format
      const allMonths = new Set<string>();
      Object.values(monthlyAverages).forEach(restaurant => {
        Object.keys(restaurant).forEach(month => allMonths.add(month));
      });

      const sortedMonths = Array.from(allMonths).sort();

      return sortedMonths.map(month => {
        const dataPoint: any = { month };
        
        Object.keys(monthlyAverages).forEach(restaurantId => {
          const restaurantData = monthlyAverages[restaurantId][month];
          const avgRating = restaurantData 
            ? (restaurantData.sum / restaurantData.count).toFixed(1)
            : null;
          
          dataPoint[restaurantNames[restaurantId] || restaurantId] = avgRating;
        });
        
        return dataPoint;
      });
    },
  });
};

export const useRestaurantCommentTrends = () => {
  const { user } = useAuth();
  
  return useQuery({
    queryKey: ["restaurant-comment-trends", user?.id],
    queryFn: async () => {
      if (!user) return [];

      const userRole = await getUserRole(user.id);
      const isSupervisor = userRole === "supervisor";

      // Get restaurants (filtered for supervisors)
      let restaurantIds: string[] = [];
      let restaurantNames: Record<string, string> = {};

      if (isSupervisor) {
        const { data: merchantRestaurants } = await supabase
          .from("merchant_restaurants")
          .select("restaurant_id")
          .eq("user_id", user.id);

        restaurantIds = merchantRestaurants?.map(mr => mr.restaurant_id) || [];
      } else {
        const { data: restaurants } = await supabase
          .from("restaurants")
          .select("id, name")
          .order("rating", { ascending: false, nullsFirst: false })
          .limit(5);

        restaurantIds = restaurants?.map(r => r.id) || [];
        restaurants?.forEach(r => {
          restaurantNames[r.id] = r.name;
        });
      }

      if (restaurantIds.length === 0) return [];

      // Get all comments for these restaurants with timestamps
      const { data: comments } = await supabase
        .from("comments")
        .select("target_id, created_at")
        .eq("target_type", "restaurant")
        .in("target_id", restaurantIds)
        .order("created_at", { ascending: true });

      if (!comments || comments.length === 0) return [];

      // Get restaurant names if not already fetched
      if (Object.keys(restaurantNames).length === 0) {
        const { data: restaurants } = await supabase
          .from("restaurants")
          .select("id, name")
          .in("id", restaurantIds);

        restaurants?.forEach(r => {
          restaurantNames[r.id] = r.name;
        });
      }

      // Group by restaurant and month, count comments
      const monthlyCounts: Record<string, Record<string, number>> = {};

      comments.forEach(comment => {
        const date = new Date(comment.created_at);
        const monthKey = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, "0")}`;
        
        if (!monthlyCounts[comment.target_id]) {
          monthlyCounts[comment.target_id] = {};
        }
        
        monthlyCounts[comment.target_id][monthKey] = (monthlyCounts[comment.target_id][monthKey] || 0) + 1;
      });

      // Convert to chart data format
      const allMonths = new Set<string>();
      Object.values(monthlyCounts).forEach(restaurant => {
        Object.keys(restaurant).forEach(month => allMonths.add(month));
      });

      const sortedMonths = Array.from(allMonths).sort();

      return sortedMonths.map(month => {
        const dataPoint: any = { month };
        
        Object.keys(monthlyCounts).forEach(restaurantId => {
          const count = monthlyCounts[restaurantId][month] || 0;
          dataPoint[restaurantNames[restaurantId] || restaurantId] = count;
        });
        
        return dataPoint;
      });
    },
  });
};

export const useActivityEngagement = () => {
  return useQuery({
    queryKey: ["activity-engagement"],
    queryFn: async () => {
      const { data: activities, error } = await supabase
        .from("activities")
        .select("id, title_en")
        .order("created_at", { ascending: false })
        .limit(10);

      if (error) throw error;

      // Get subscriber counts for each activity
      const activitiesWithEngagement = await Promise.all(
        (activities || []).map(async (activity) => {
          const [subscribers, comments] = await Promise.all([
            supabase
              .from("subscriptions")
              .select("*", { count: "exact", head: true })
              .eq("activity_id", activity.id),
            supabase
              .from("comments")
              .select("*", { count: "exact", head: true })
              .eq("target_id", activity.id)
              .eq("target_type", "activity"),
          ]);

          return {
            name: activity.title_en,
            subscribers: subscribers.count || 0,
            comments: comments.count || 0,
          };
        })
      );

      return activitiesWithEngagement;
    },
  });
};

export const useDeleteActivity = () => {
  const { toast } = useToast();
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async (activityId: string) => {
      // Delete associated restaurants first
      await supabase
        .from("activity_restaurants")
        .delete()
        .eq("activity_id", activityId);

      // Delete the activity
      const { error } = await supabase
        .from("activities")
        .delete()
        .eq("id", activityId);

      if (error) throw error;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["activities"] });
      queryClient.invalidateQueries({ queryKey: ["admin-activities"] });
      toast({
        title: i18n.t("common.success"),
        description: i18n.t("toast.activityDeleted"),
      });
    },
    onError: () => {
      toast({
        title: i18n.t("common.error"),
        description: i18n.t("toast.activityDeleteFailed"),
        variant: "destructive",
      });
    },
  });
};

