export const useMuteUser = () => {
  const { toast } = useToast();
  const queryClient = useQueryClient();
  const { user } = useAuth();

  return useMutation({
    mutationFn: async ({ userId, muteReason, mutedUntil }: { userId: string; muteReason?: string; mutedUntil?: string }) => {
      const { error } = await supabase
        .from("profiles")
        .update({
          is_muted: true,
          mute_reason: muteReason || null,
          muted_at: new Date().toISOString(),
          muted_by: user?.id,
          muted_until: mutedUntil || null,
        })
        .eq("id", userId);

      if (error) throw error;

      // Get user's language preference for notification
      const { data: userProfile } = await supabase
        .from("profiles")
        .select("language_preference")
        .eq("id", userId)
        .single();

      const userLang = userProfile?.language_preference || "en";
      
      // Format duration for notification
      let durationText = "";
      if (mutedUntil) {
        const mutedUntilDate = new Date(mutedUntil);
        if (userLang === "zh") {
          durationText = `禁言至: ${mutedUntilDate.toLocaleDateString("zh-CN")}`;
        } else if (userLang === "pt") {
          durationText = `Silenciado até: ${mutedUntilDate.toLocaleDateString("pt-PT")}`;
        } else {
          durationText = `Muted until: ${mutedUntilDate.toLocaleDateString("en-US")}`;
        }
      } else {
        if (userLang === "zh") {
          durationText = "永久禁言";
        } else if (userLang === "pt") {
          durationText = "Silenciamento permanente";
        } else {
          durationText = "Permanent mute";
        }
      }

      // Create notification for the muted user
      let notificationTitle = "";
      let notificationContent = "";
      
      if (userLang === "zh") {
        notificationTitle = "账号已被禁言";
        notificationContent = `${durationText}。原因: ${muteReason || "违反社区规则"}`;
      } else if (userLang === "pt") {
        notificationTitle = "Conta silenciada";
        notificationContent = `${durationText}. Motivo: ${muteReason || "Violação das regras da comunidade"}`;
      } else {
        notificationTitle = "Account muted";
        notificationContent = `${durationText}. Reason: ${muteReason || "Community guidelines violation"}`;
      }

      await supabase.from("notifications").insert({
        user_id: userId,
        type: "activity_update",
        title: notificationTitle,
        content: notificationContent,
        link: "/profile",
      });
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["admin-users"] });
      queryClient.invalidateQueries({ queryKey: ["muted-users"] });
      toast({
        title: i18n.t("common.success"),
        description: i18n.t("toast.userMuted"),
      });
    },
    onError: () => {
      toast({
        title: i18n.t("common.error"),
        description: i18n.t("toast.userMuteFailed"),
        variant: "destructive",
      });
    },
  });
};

