export const useActivityShareTrends = () => {
  return useQuery({
    queryKey: ["activity-share-trends"],
    queryFn: async () => {
      // Get top 5 activities by share count
      const { data: activities } = await supabase
        .from("activities")
        .select("id, title_zh, title_en, title_pt")
        .eq("visible", true)
        .limit(10);

      if (!activities || activities.length === 0) return [];

      const activityIds = activities.map(a => a.id);
      const activityNames: Record<string, string> = {};
      activities.forEach(a => {
        activityNames[a.id] = a.title_zh || a.title_en;
      });

      // Get all shares for these activities
      const { data: shares } = await supabase
        .from("activity_shares")
        .select("activity_id, created_at")
        .in("activity_id", activityIds)
        .order("created_at", { ascending: true });

      if (!shares || shares.length === 0) return [];

      // Group by activity and month
      const monthlyShares: Record<string, Record<string, number>> = {};

      shares.forEach(share => {
        const date = new Date(share.created_at);
        const monthKey = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, "0")}`;
        
        if (!monthlyShares[share.activity_id]) {
          monthlyShares[share.activity_id] = {};
        }
        
        monthlyShares[share.activity_id][monthKey] = 
          (monthlyShares[share.activity_id][monthKey] || 0) + 1;
      });

      // Get all months
      const allMonths = new Set<string>();
      Object.values(monthlyShares).forEach(activity => {
        Object.keys(activity).forEach(month => allMonths.add(month));
      });

      const sortedMonths = Array.from(allMonths).sort();

      // Filter to only include activities that have shares
      const activitiesWithShares = Object.keys(monthlyShares);

      return sortedMonths.map(month => {
        const dataPoint: any = { month };
        
        activitiesWithShares.forEach(activityId => {
          const shareCount = monthlyShares[activityId][month] || 0;
          const name = activityNames[activityId]?.substring(0, 15) || activityId;
          dataPoint[name] = shareCount;
        });
        
        return dataPoint;
      });
    },
  });
};

export const useActivityShareStats = () => {
  return useQuery({
    queryKey: ["activity-share-stats"],
    queryFn: async () => {
      // Get all activities with their share counts
      const { data: activities } = await supabase
        .from("activities")
        .select("id, title_zh, title_en, title_pt")
        .eq("visible", true);

      if (!activities || activities.length === 0) return [];

      // Get share counts for each activity
      const activitiesWithShares = await Promise.all(
        activities.map(async (activity) => {
          const { count } = await supabase
            .from("activity_shares")
            .select("*", { count: "exact", head: true })
            .eq("activity_id", activity.id);

          return {
            id: activity.id,
            name: (activity.title_zh || activity.title_en).substring(0, 15),
            shares: count || 0,
          };
        })
      );

      // Sort by shares descending and take top 10
      return activitiesWithShares
        .sort((a, b) => b.shares - a.shares)
        .slice(0, 10);
    },
  });
};
